import React from "react";

import Layout from "components/layout";
import Multipliers from "components/trinet/multipliers";
import TestimonialSection from "components/trinet/testimonials";
import AlternatingDetailTitle from "components/trinet/title";
import Hero from "components/trinet/hero";
import colors from "styles/colors";
import TrustedByStyled from "components/TrustedByStyled";
import { compose, lifecycle, StateHandler, withStateHandlers } from "recompose";
import {
  rewards,
  cardHolders,
  iconValuePropListProps,
  logos,
  detailsProps,
  detailsMainTitle,
  signUp,
  testimonials,
} from "components/trinet/messages";
import IconValuePropList from "components/reusable/organisms/IconValuePropList";
import AlternatingDetailList from "components/reusable/organisms/AlternatingDetailList";
import { SignUp } from "components/trinet/signup";
import { Footer } from "components/trinet/footer";
import Modal from "components/trinet/modal";
import { clearAllBodyScrollLocks } from "body-scroll-lock";

interface Props {
  location: Location;
  selected: number;
  changeTab: Function;
  closeModal: Function;
  showModal: (event: React.MouseEvent) => void;
  shouldShowModal: boolean;
}

interface CycleProps {
  changeTab: StateHandler<{
    selected: number;
  }>;
}

const Trinet = ({
  location,
  selected,
  changeTab,
  shouldShowModal,
  closeModal,
  showModal,
}: Props) => {
  let hash = "startup";
  if (selected === 1) {
    hash = "lifescience";
  }
  return (
    <Layout
      location={location}
      whiteContentBackground
      hideDefaultHeader
      hideDefaultFooter
      title="TriNet & Brex | Brex Partners"
      description="TriNet customers unlock a whole new set of points multiplers on most frequent purchases with a Brex corporate card."
      indexPage={false}
    >
      <Hero changeTab={changeTab} selected={selected} showModal={showModal} />
      <Multipliers
        rewards={cardHolders[hash]}
        backgroundColor={colors.blue}
        titleColor={colors.white}
        whiteContent
      />
      <Multipliers
        rewards={rewards[hash]}
        backgroundColor={colors.white}
        titleColor={colors.black}
      />
      <IconValuePropList {...iconValuePropListProps[hash]} centered />
      <TrustedByStyled logos={logos[hash]} lightBackground />
      <AlternatingDetailTitle alternatingTitle={detailsMainTitle[hash]} />
      <AlternatingDetailList
        notUseGradient
        details={detailsProps[hash]}
        firstDetailIsImageFirstDesktop={undefined}
        imageFirstMobile
        testimonial={undefined}
      />
      <SignUp signUp={signUp[hash]} showModal={showModal} />
      <TestimonialSection testimonial={testimonials[hash]} />
      <Footer showModal={showModal} />
      <Modal shouldShowModal={shouldShowModal} closeModal={closeModal} />
    </Layout>
  );
};

export default compose<Props, {}>(
  withStateHandlers(
    () => ({
      selected: 0,
      shouldShowModal: false,
    }),
    {
      changeTab: () => (item: number) => {
        let hash = "startup";
        if (item === 1) {
          hash = "lifescience";
        }
        if (typeof window !== "undefined") {
          window.location.hash = hash;
        }
        return {
          selected: item,
        };
      },
      showModal: () => (e) => {
        e.stopPropagation();
        return { shouldShowModal: true };
      },
      closeModal: () => () => {
        clearAllBodyScrollLocks();
        return { shouldShowModal: false };
      },
    },
  ),
  lifecycle<CycleProps, { selected: number; shouldShowModal: boolean }, {}>({
    componentDidMount() {
      let selectedFromHash = 0;
      if (typeof window !== "undefined") {
        if (window.location.hash === "#lifescience") {
          selectedFromHash = 1;
        }
        if (window.location.hash.length === 0) {
          if (typeof window !== "undefined") {
            window.location.hash = "startup";
          }
        }
      }

      this.props.changeTab(selectedFromHash);
    },
  }),
)(Trinet);
