import React from "react";
import {
  Container,
  SmallCaption,
  Title,
  Body,
  MultiplierNumber,
  MultiplierContainer,
  RewardsContainer,
  TitleWrapper,
} from "./elements";

interface MultipliersProps {
  rewards: {
    title: string;
    content: Array<{
      title: string;
      noLastLetter: boolean;
      number: string;
      body: string;
      display: boolean;
    }>;
  };
  backgroundColor: string;
  titleColor: string;
  whiteContent?: boolean;
}

const Multipliers = ({
  rewards,
  backgroundColor,
  titleColor,
  whiteContent = false,
}: MultipliersProps) => (
  <Container backgroundColor={backgroundColor}>
    <TitleWrapper>
      <Title titleColor={titleColor}>{rewards.title}</Title>
    </TitleWrapper>
    <RewardsContainer>
      {rewards.content.map((reward) => (
        <MultiplierContainer display={reward.display} key={reward.title}>
          <MultiplierNumber whiteContent={whiteContent}>
            {reward.noLastLetter ? reward.number : `${reward.number}x`}
          </MultiplierNumber>
          <SmallCaption whiteContent={whiteContent}>
            {reward.title}
          </SmallCaption>
          <Body whiteContent={whiteContent}>{reward.body}</Body>
        </MultiplierContainer>
      ))}
    </RewardsContainer>
  </Container>
);

export default Multipliers;
