import React from "react";
import ReactDOM from "react-dom";
import {
  compose,
  lifecycle,
  StateHandler,
  StateHandlerMap,
  withHandlers,
} from "recompose";

import closeIcon from "assets/img/icons/black/dark-fill/close.svg";
import {
  Body,
  ModalInner,
  ModalOuter,
  Title,
  ButtonGroup,
  StyledSignUpReferenceLink,
  LinkContainer,
  Arrow,
  CloseIcon,
} from "./elements";

interface StateProps {
  shouldShowModal?: boolean;
}

type StateHandlerProps = StateHandlerMap<StateProps> & {
  showModal: StateHandler<StateProps>;
  closeModal: StateHandler<StateProps>;
};

interface HandlerProps {
  onModalInnerClick: React.MouseEventHandler<HTMLDivElement>;
}

type InternalProps = StateProps & StateHandlerProps & HandlerProps;

interface ExternalProps {
  shouldShowModal?: boolean;
  closeModal: Function;
}

type MarketoFormProps = InternalProps & ExternalProps;

const _Modal: React.SFC<MarketoFormProps> = ({
  shouldShowModal,
  closeModal,
  onModalInnerClick,
}) => (
  <React.Fragment>
    {shouldShowModal &&
      ReactDOM.createPortal(
        <ModalOuter onClick={closeModal}>
          <ModalInner onClick={onModalInnerClick}>
            <>
              <CloseIcon src={closeIcon} onClick={closeModal} />
              <Title>Are you a Trinet customer?</Title>
              <Body>
                <ButtonGroup>
                  <StyledSignUpReferenceLink
                    companyType={undefined}
                    refCode="trinetcustomer"
                  >
                    <LinkContainer>
                      I’m a TriNet customer and want this exclusive deal
                      <Arrow />
                    </LinkContainer>
                  </StyledSignUpReferenceLink>
                </ButtonGroup>
                <ButtonGroup>
                  <StyledSignUpReferenceLink
                    companyType={undefined}
                    refCode="trinetlead"
                  >
                    <LinkContainer>
                      I want to sign up with Brex and learn more about TriNet
                      <Arrow />
                    </LinkContainer>
                  </StyledSignUpReferenceLink>
                </ButtonGroup>
                <ButtonGroup>
                  <StyledSignUpReferenceLink
                    companyType={undefined}
                    refCode="trinetoptout"
                  >
                    <LinkContainer>
                      I’m only interested in signing up with Brex right now
                      <Arrow />
                    </LinkContainer>
                  </StyledSignUpReferenceLink>
                </ButtonGroup>
              </Body>
            </>
          </ModalInner>
        </ModalOuter>,
        document.body,
      )}
  </React.Fragment>
);

const Modal = compose<MarketoFormProps, ExternalProps>(
  withHandlers<MarketoFormProps, HandlerProps>(() => ({
    onModalInnerClick: () => (e) => e.stopPropagation(),
    closeOnEscape: ({ closeModal }) => (e: { code: string }) => {
      if (e.code === "Escape") {
        closeModal();
      }
    },
  })),
  lifecycle<MarketoFormProps, {}, {}>({
    componentDidMount() {
      if (this.props.shouldShowModal) {
        document.body.className = "disabled-scroll-absolute";
      } else document.body.className = "";

      document.addEventListener("keydown", this.props.closeOnEscape);
    },
    componentWillUnmount() {
      document.removeEventListener("keydown", this.props.closeOnEscape);
    },
  }),
)(_Modal);

export default Modal;
